import React from "react";

const Navabar = ({ filterItem, MenuList }) => {
  return (
    <>
      <nav className="navbar">
        <span class="logo">
          <a href="">
            <img
              src="https://i.imgur.com/3Ay7GzN.png"
              height="150"
              width="150"
              alt="Logo"
            />
          </a>
        </span>
        <div className="btn-group">
          {MenuList.map((currentElement) => {
            return (
              <>
                <button
                  className="btn-group__item"
                  onClick={() => filterItem(currentElement)}
                >
                  {currentElement}
                </button>
              </>
            );
          })}

          {/* <button className="btn-group__item" onClick={() => setMenu(Menu)}>
            All
          </button> */}
        </div>
      </nav>
    </>
  );
};
export default Navabar;
